import React from 'react';
import { Link } from "react-router-dom";
import { Container, Navbar, Nav} from "react-bootstrap";


const NavTop = () => {


    return (
        <Navbar className= "navbar" expand="lg">
            <Container>
                <Navbar.Brand href="#home" className="brand">RZU PALONKO</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="ms-auto">
                        <Nav.Link>
                            <Link to="/">Home</Link>
                        </Nav.Link>
                        <Nav.Link>
                            <Link to="/about">About</Link>
                        </Nav.Link>
                        <Nav.Link>
                            <Link to="/cigarette">Palisz? Kliknij</Link>
                        </Nav.Link>
                        <Nav.Link>
                            <Link to="/propose">Twj Cel</Link>
                        </Nav.Link>
                        <Nav.Link>
                            <Link to="/statistics">Statystyki</Link>
                        </Nav.Link>
                    </Nav>
                    <Nav className="ms-auto">
                      <button className= "btn btn-success">Sing Up</button>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
};

export default NavTop;